.SearchInput.container, .SearchInput input {
    background-color: rgb(235, 235, 235);
}

.SearchInput.container {  
    border-radius: 25px;
    padding: 5px 15px;
    margin: 20px 0px;
    width: 80%;
    min-width: 280px;
    height: 35px;
    max-width: 500px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.SearchInput input {
    border: none;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    height: 100%;
    flex: 1;
}

.SearchInput input:focus {
    outline: none;
}

.SearchInput.delete {
    background: none;
    border: none;
    width: 20px;
}

.SearchInput.delete:hover {
    cursor: pointer;
}

.SearchInput.search-logo {
    margin-right: 15px;
    margin-left: 5px;
}