.Shortcuts.container {
     width: 90%;
     display: flex;
     flex-direction: row;
     justify-content: center;
     flex-wrap: wrap; /* I'm gonna fix this latter */
}

.Shortcut.container {
     width: 80px; 
     border-radius: 5px;
     margin: 5px;
     padding: 10px;
     display: flex;
     flex-direction: column;
     align-items: center;
}

.Shortcut.container:hover {
     cursor: pointer;
     background-color: rgb(233, 232, 232);
}

.Shortcut > div {
     width: 45px;
     height: 45px;
     border-radius: 100%;
     background-color: rgb(241, 241, 241);
     display: flex;
     align-items: center;
     justify-content: center;
}
.Shortcut span {
     text-align: center;
     margin-top: 5px;
     font-size: 13px;
}